<template>
  <v-card class="mx-auto mt-6" max-width="900" min-height="200" rounded="lg" :elevation="hover ? 24 : 6">
    <v-row>
      <v-col cols="12">
        <v-container>
          <v-badge :value="book.read" icon="mdi-check-circle" left color="green" overlap offset-y="3">
            <router-link class="title2" :to="bookUrl">
              {{ book.title }}
            </router-link>
          </v-badge>
          <div>
            <router-link class="author2" :to="{ name: 'books-author', params: { author: author } }" v-for="(author, i) in authors" :key="i">
              {{ author }}{{ commaInbetween(i, authors) }}
            </router-link>
          </div>
        </v-container>
        <v-card-text :class="marginCover[1]">
          <v-row dense no-gutters>
            <v-col cols="12" md="10" sm="10" xs="5" order="2" order-sm="1">
              <v-row dense no-gutters>
                <v-col cols="4" md="3" sm="3" xs="5">Rok wydania:</v-col>
                <v-col class="attribute">{{ book.publicationDate }}</v-col>
              </v-row>
              <v-row dense no-gutters>
                <v-col cols="4" md="3" sm="3" xs="5">Liczba stron:</v-col>
                <v-col class="attribute">{{ book.pages }}</v-col>
              </v-row>
              <v-row dense no-gutters>
                <v-col cols="4" md="3" sm="3" xs="5">Oprawa:</v-col>
                <v-col class="attribute">{{ book.cover }}</v-col>
              </v-row>
              <v-row dense no-gutters>
                <v-col cols="4" md="3" sm="3" xs="5">Wydawnictwo:</v-col>
                <v-col class="attribute">{{ book.publisher }}</v-col>
              </v-row>
              <v-row dense no-gutters>
                <v-col cols="4" md="3" sm="3" xs="5">Tłumacz:</v-col>
                <v-col class="attribute"
                  ><span v-for="(translator, i) in translators" :key="i">{{ attributesWithComma(i, translators) }} </span></v-col
                >
              </v-row>
              <v-row dense no-gutters>
                <v-col cols="4" md="3" sm="3" xs="4">Tytuł oryginalny:</v-col>
                <v-col class="attribute" align-self="center">{{ book.originalTitle }}</v-col>
              </v-row>
            </v-col>
            <v-col cols="12" md="2" sm="2" xs="2" order="1" order-sm="2" align="center">
              <v-card :class="marginCover[0]" elevation="10" max-height="210" max-width="150" :to="{ name: 'book', params: { id: book._id } }">
                <v-img max-height="210" max-width="150" :src="book.image"></v-img>
              </v-card>
            </v-col>
          </v-row>
        </v-card-text>
      </v-col>
    </v-row>
    <v-row dense no-gutters>
      <v-col>
        <v-chip-group class="ml-3">
          <v-icon>mdi-tag</v-icon>
          <v-chip class="mb-1" v-for="(tag, i) in tags" :key="i" :to="{ name: 'books-tag', params: { tag: tag } }">
            {{ tag }}
          </v-chip>
        </v-chip-group>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import { attributesWithComma, commaInbetween } from "../utils/declination";

export default {
  name: "BookTile",
  props: ["book"],
  data() {
    return {
      hover: "",
      bookUrl: `/book/${this.book._id}`,
      authors: [],
      translators: [],
      tags: [],
    };
  },
  computed: {
    marginCover() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return ["mt-n6 mb-2"];
        case "sm":
          return ["mt-n8", "mt-n5"];
        case "md":
          return ["mt-n8 mr-4", "mt-n5"];
        case "lg":
          return ["mt-n8 mr-4", "mt-n5"];
        case "xl":
          return ["mt-n8 mr-4", "mt-n5"];
      }
    },
  },
  methods: {
    attributesWithComma(index, array) {
      return attributesWithComma(index, array);
    },
    commaInbetween(index, array) {
      return commaInbetween(index, array);
    },
  },
  created() {
    this.authors = this.book.authors.split(",");
    this.translators = this.book.translators.split(",");
    this.tags = this.book.tags.split(",");
  },
};
</script>

<style scoped>
.title2 {
  all: unset;
  text-justify: inter-word;
  cursor: pointer;
  font: bold 25px Roboto;
}
.author {
  color: rgb(78, 77, 77);
  font: 18px Georgia, serif;
  margin-top: 0px;
}
.author2 {
  all: unset;
  color: rgb(78, 77, 77);
  font: 18px Georgia, serif;
  /* margin-left: 12px; */
  margin-top: 0px;
  cursor: pointer;
}
.attribute {
  font-weight: bold;
}
</style>
