<template>
  <v-container>
    <v-card class="mx-auto" max-width="700">
      <v-card-title>Dodaj książę</v-card-title>
      <v-divider></v-divider>
      <v-form ref="form" @submit.prevent="submitForm" class="pa-5" enctype="multipart/form-data">
        <v-text-field label="Tytuł" v-model="book.title" :rules="rules"></v-text-field>
        <v-combobox v-model="book.authors" :items="authorSearch" label="Autor" multiple chips :rules="rules"></v-combobox>
        <v-combobox v-model="book.publisher" :items="publisherSearch" label="Wydawnictwo" chips></v-combobox>
        <v-text-field label="Rok wydania" v-model="book.publicationDate"></v-text-field>
        <v-text-field label="Liczba stron" v-model="book.pages"></v-text-field>
        <v-text-field label="Okładka" v-model="book.cover"></v-text-field>
        <v-text-field label="Cena zakupu" v-model="book.purchasePrice"></v-text-field>
        <v-text-field label="Cena detaliczna" v-model="book.retailPrice"></v-text-field>
        <v-combobox v-model="book.translators" :items="translatorSearch" label="Tłumacz" multiple chips></v-combobox>
        <v-text-field v-model="book.originalTitle" label="Tytuł oryginalny"></v-text-field>
        <v-combobox v-model="book.tags" :items="tagsSearch" label="Tagi" multiple chips></v-combobox>
        <v-checkbox v-model="book.read" label="Przeczytana"></v-checkbox>

        <v-row>
          <v-col cols="12" sm="6" md="4">
            <v-menu v-model="menu2" :close-on-content-click="true" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="book.purchaseDate"
                  prepend-icon="mdi-calendar"
                  label="Data zakupu"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="book.purchaseDate" @input="menu2 = false"></v-date-picker>
            </v-menu>
          </v-col>
          <v-spacer></v-spacer>
        </v-row>
        <v-file-input @change="selectFile" show-size counter multiple label="Dodaj okładkę"></v-file-input>
        <v-btn type="submit" class="mt-3" color="primary">Dodaj książkę</v-btn>
      </v-form>
    </v-card>
  </v-container>
</template>

<script>
import API from "../api";

export default {
  data() {
    return {
      rules: [(value) => !!value || "To pole jest wymagane."],
      book: {
        title: "",
        authors: "",
        publisher: "",
        publicationDate: "",
        pages: "",
        cover: "",
        purchasePrice: "",
        retailPrice: "",
        read: false,
        purchaseDate: new Date().toISOString().substr(0, 10),
        translators: "",
        originalTitle: "",
        tags: "",
        image: "https://i.imgur.com/rpVYMUX.jpeg",
      },
      image: "https://i.imgur.com/rpVYMUX.jpeg",
      menu: false,
      modal: false,
      menu2: false,
      authorSearch: [],
      publisherSearch: [],
      translatorSearch: [],
      tagsSearch: [],
    };
  },
  methods: {
    selectFile(file) {
      this.image = file[0];
    },

    async submitForm() {
      const formData = new FormData();
      formData.append("image", this.image);
      formData.append("title", this.book.title);
      formData.append("authors", this.book.authors);
      formData.append("publisher", this.book.publisher);
      formData.append("publicationDate", this.book.publicationDate);
      formData.append("pages", this.book.pages);
      formData.append("cover", this.book.cover);
      formData.append("purchasePrice", this.book.purchasePrice);
      formData.append("retailPrice", this.book.retailPrice);
      formData.append("read", this.book.read);
      formData.append("purchaseDate", this.book.purchaseDate);
      formData.append("translators", this.book.translators);
      formData.append("originalTitle", this.book.originalTitle);
      formData.append("tags", this.book.tags);
      if (this.$refs.form.validate()) {
        const response = await API.addBook(formData);
        this.$router.push({
          name: "books",
          params: { message: response.message },
        });
      }
    },
  },
  async created() {
    if (localStorage.getItem("token") === null) {
      this.$router.push("/login");
    }

    const data = await API.getAutocompleteData();
    this.authorSearch = Array.from(data.authors);
    this.publisherSearch = Array.from(data.publishers);
    this.translatorSearch = Array.from(data.translators);
    this.tagsSearch = Array.from(data.tags);
  },
};
</script>
