<template>
  <v-container>
    <v-card class="mx-auto mt-3" width="800" :elevation="hover ? 24 : 6">
      <v-container>
        <div class="title">
          {{ book.title }}
        </div>
        <div>
          <router-link class="author" :to="{ name: 'books-author', params: { author: author } }" v-for="(author, i) in authors" :key="i">
            {{ author }}{{ commaInbetween(i, authors) }}
          </router-link>
        </div>
      </v-container>

      <v-row>
        <v-col cols="12" sm="8" order="2" order-sm="1">
          <v-card-text>
            <v-row dense no-gutters>
              <v-col cols="12" md="12" sm="12" xs="5" order="2" order-sm="1">
                <v-row dense no-gutters>
                  <v-col cols="4" md="4" sm="4" xs="5">Rok wydania:</v-col>
                  <v-col class="attribute">{{ book.publicationDate }}</v-col>
                </v-row>
                <v-row dense no-gutters>
                  <v-col cols="4" md="4" sm="4" xs="5">Liczba stron:</v-col>
                  <v-col class="attribute">{{ book.pages }}</v-col>
                </v-row>
                <v-row dense no-gutters>
                  <v-col cols="4" md="4" sm="4" xs="5">Oprawa:</v-col>
                  <v-col class="attribute">{{ book.cover }}</v-col>
                </v-row>
                <v-row dense no-gutters>
                  <v-col cols="4" md="4" sm="4" xs="5">Wydawnictwo:</v-col>
                  <v-col class="attribute">{{ book.publisher }}</v-col>
                </v-row>
                <v-row dense no-gutters>
                  <v-col cols="4" md="4" sm="4" xs="5">Cena zakupu:</v-col>
                  <v-col class="attribute">{{ book.purchasePrice.toFixed(2) }} zł</v-col>
                </v-row>
                <v-row dense no-gutters>
                  <v-col cols="4" md="4" sm="4" xs="5">Cena detaliczna:</v-col>
                  <v-col class="attribute" align-self="center">{{ book.retailPrice.toFixed(2) }} zł</v-col>
                </v-row>
                <v-row dense no-gutters>
                  <v-col cols="4" md="4" sm="4" xs="5">Data zakupu:</v-col>
                  <v-col class="attribute">{{ book.purchaseDate }}</v-col>
                </v-row>
                <v-row dense no-gutters>
                  <v-col cols="4" md="4" sm="4" xs="5">Przeczytana:</v-col>
                  <v-col class="attribute">{{ book.read ? "tak" : "nie" }}</v-col>
                </v-row>
                <v-row dense no-gutters>
                  <v-col cols="4" md="4" sm="4" xs="5">Tłumacz:</v-col>
                  <v-col class="attribute"
                    ><span v-for="(translator, i) in translators" :key="i">{{ attributesWithComma(i, translators) }} </span></v-col
                  >
                </v-row>
                <v-row dense no-gutters>
                  <v-col cols="4" md="4" sm="4" xs="4">Tytuł oryginalny:</v-col>
                  <v-col class="attribute" align-self="center">{{ book.originalTitle }}</v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-card-text>
        </v-col>

        <v-col cols="12" sm="4" order="1" order-sm="2" align="center">
          <v-card class="mt-4" max-width="150" elevation="10">
            <v-img max-height="210" max-width="150" :src="book.image"></v-img>
          </v-card>
        </v-col>
      </v-row>
      <v-card-subtitle class="mt-n2">
        <v-icon>mdi-tag</v-icon>
        <v-chip class="ml-1 mb-1" v-for="(tag, i) in tags" :key="i" :to="{ name: 'books-tag', params: { tag: tag } }">
          {{ tag }}
        </v-chip>
      </v-card-subtitle>
      <v-row v-if="logged">
        <v-col class="d-flex justify-end mx-n3">
          <v-btn color="blue" text :to="{ name: 'add-quote', params: { id: book._id } }">Dodaj cytat</v-btn>
          <v-btn color="success" text :to="{ name: 'edit-book', params: { id: book._id } }">Edytuj</v-btn>
          <v-btn color="red" text @click="removeBook(book._id)">Usuń</v-btn>
        </v-col>
      </v-row>
    </v-card>
    <v-divider class="ml-5 mt-5"></v-divider>
    <h2 v-if="quotes.length != 0">Cytaty</h2>
    <quote-tile v-for="quote in quotes" :key="quote.id" :quote="quote"></quote-tile>
  </v-container>
</template>

<script>
import API from "../api";
import { attributesWithComma, commaInbetween } from "../utils/declination";

export default {
  data() {
    return {
      book: {
        purchasePrice: 0,
        retailPrice: 0,
      },
      quotes: [],
      authors: [],
      translators: [],
      tags: [],
      hover: "",
      logged: false,
    };
  },
  async created() {
    if (localStorage.getItem("token") !== null) {
      this.logged = true;
    }

    const bookResponse = await API.getBookById(this.$route.params.id);
    this.book = bookResponse;
    this.book.purchaseDate = bookResponse.purchaseDate.substr(0, 10);
    const quoteResponse = await API.getQuotesByBookId(this.book._id);
    this.quotes = quoteResponse;
    this.authors = this.book.authors.split(",");
    this.translators = this.book.translators.split(",");
    this.tags = this.book.tags.split(",");
  },
  methods: {
    async removeBook(id) {
      const response = await API.deleteBook(id);
      this.$router.push({ name: "books", params: { message: response.message } });
    },
    attributesWithComma(index, array) {
      return attributesWithComma(index, array);
    },
    commaInbetween(index, array) {
      return commaInbetween(index, array);
    },
  },
};
</script>

<style scoped>
.tags {
  color: rgb(78, 77, 77);
  font: 15px Georgia, serif;
  margin-left: 5px;
  margin-top: 0px;
  margin-bottom: -2pc;
}
.tags-first {
  color: rgb(78, 77, 77);
  font: 15px Georgia, serif;
  margin-left: 2pc;
  margin-top: 0px;
  margin-bottom: -2pc;
}
.title {
  all: unset;
  text-justify: inter-word;
  font: bold 25px Roboto;
}
.author {
  all: unset;
  color: rgb(78, 77, 77);
  font: 18px Georgia, serif;
  /* margin-left: 12px; */
  margin-top: 0px;
  cursor: pointer;
}
.attribute {
  font-weight: bold;
}
.v-card__text,
.v-card__title {
  word-break: normal;
}
</style>
